.hederList {
  display: flex;
  align-items: center;
  gap: 20px;
  min-height: 40px;
}

.link {
  display: block;
  text-decoration: none;
  color: inherit;
  line-height: 30px;
  padding-left: 5px;
  padding-right: 5px;

  cursor: pointer;
  border-radius: 4px;
  background-color: #fff;
  border-color: transparent;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.link:hover {
  background-color: #f53602;
  color: #fff;
}

.userProfileLink {
  display: flex;
  gap: 10px;
  align-items: center;
  text-decoration: none;
}

.button {
  height: 30px;
}
